import React from 'react';
import './Home.css';

export class Home extends React.Component {

    HandleClick(id) {
        if(document.getElementById(id).hidden)
        {
            document.getElementById(id).hidden = false;
        }else{
            document.getElementById(id).hidden = true;
        }
    }

    CheckBoxChanged(name) {
        if(document.getElementById("check"+name).checked)
        {
            document.getElementById("collapse"+name).hidden = true;
        }
        else
        {
            document.getElementById("collapse"+name).hidden = false;
        }
    }

    render() {
        return (
            <ul className="list-group">
                {this.props.userdetails.map(
                    (user) => 
                        <div className="container" key={"container-"+user.name}>
                            <li className="list-group-item" key={"li-"+user.name}>
                            <div>
                                <div className="card-header">
                                    <input type="checkbox" className="big-checkbox" id={"check"+user.name} onChange={this.CheckBoxChanged.bind(this,user.name)}/>
                                    <img className="img-prof" src={user.imgsrc} width="50" height="50" alt={user.name}/>
                                    <button className="btn btn-link" onClick={this.HandleClick.bind(this,"collapse"+user.name)} type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                                        {user.name}
                                    </button>&nbsp;&nbsp;&nbsp;
                                    Age:&nbsp;<span className="badge badge-pill badge-info">{user.age}</span>
                                </div>

                                <div id={"collapse"+user.name} className="collapse show" aria-labelledby="headingOne" data-parent="#accordionExample">
                                    <div className="card-body">
                                        <div className="card-header">
                                            Description: 
                                        </div>
                                        <div className="card-body">
                                                {user.desc}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            </li>
                        </div>
                    )
                }
            </ul>
        );
    }
}